import { useState, useEffect, useMemo } from 'react';
import { Input, Select } from 'antd';
import { CloseCircleTwoTone } from '@ant-design/icons';

import Table from './components/Table';

import speed from './speed.json';
import speed_mod from './speed_mod.json';
import speed_v from './speed_v.json';

import bilibili from './img/bilibili.png';
import weibo from './img/weibo.png';
import github from './img/github.png';
import logo from './img/logo.png';

import 'antd/dist/antd.min.css';

import styles from './App.module.css';

const App = () => {
    // 
    const handleData = (data) => {
        return data
            // 
            .sort((a, b) => a.speed * 1000 - b.speed * 1000)
            .map((item, index) => {
                // key+1
                if (!item.key) {
                    item.key = index + 1;
                }

                // B
                if (item.BID) {
                    item.BURL = `https://www.bilibili.com/video/${item.BID}`;
                }

                return item;
            });
    };

    const speedData = useMemo(() => handleData(speed), []);
    const speedDataMod = useMemo(() => handleData(speed_mod), []);
    const speedDataV = useMemo(() => handleData(speed_v), []);

    const defaultData = useMemo(() => (
        {
            speed: speedData,
            speedMod: speedDataMod,
            speedV: speedDataV
        }
    ), [speedData, speedDataMod, speedDataV]);

    // state
    const [rankData, setRankData] = useState(defaultData);

    // 
    const [filter, setFilter] = useState({
        name: '',
        key: 'all'
    });

    useEffect(() => {
        const tempData = {};

        // 
        const dataFilter = (data) => {
            for (const k in data) {
                tempData[k] = data[k].filter((item) => {
                    const reg = new RegExp(filter.name, 'i');
                    return reg.test(item.car);
                }).filter((item) => {
                    switch (filter.key) {
                        case 'all':
                            return item;
                        case 'suv':
                            return item[filter.key] === true;
                        case 'ev':
                            return item.powertrain === 'E';
                        default:
                            throw new Error('Error!');
                    }
                });
            }

            return tempData;
        };

        setRankData(dataFilter(defaultData));

    }, [defaultData, filter]);

    // state
    const [table, setTable] = useState('original');

    // 
    const [descStatus, setDescStatus] = useState(false);

    // 
    const [pagination, setPagination] = useState(false);

    // state
    const rankDataSelector = () => {
        switch (table) {
            case 'original':
                return rankData.speed;
            case 'mod':
                return rankData.speedMod;
            case 'vip':
                return rankData.speedV;
            default:
                throw new Error('');
        }
    };

    return (
        <>
            <div className={styles.main}>
                <Title>
                    <Search
                        filter={filter}
                        setFilter={setFilter}
                    />
                </Title>

                <Table
                    styles={styles}
                    rankData={rankDataSelector()}
                    pagination={pagination}
                    title={
                        <SwitchBox
                            table={table}
                            setTable={setTable}
                            pagination={pagination}
                            setPagination={setPagination}
                        />
                    }
                >
                </Table>

                <Description
                    descStatus={descStatus}
                    setDescStatus={setDescStatus}
                />
            </div>

            <Footer />
        </>
    );
};

const Search = (
    {
        filter,
        setFilter
    }
) => {
    const { Option } = Select;

    return (
        <div className={styles.search}>
            <Input
                addonBefore=""
                placeholder=""
                style={{
                    minWidth: '175px'
                }}
                onChange={(e) => setFilter(() => (
                    {
                        ...filter,
                        name: e.target.value
                    }
                ))}
                allowClear
            />
            <Select
                defaultValue={'all'}
                style={{
                    paddingLeft: '10px',
                    minWidth: '115px'
                }}
                onChange={(val) => setFilter(() => (
                    {
                        ...filter,
                        key: val
                    }
                ))}
            >
                <Option value="all"></Option>
                <Option value="suv">SUV</Option>
                <Option value="ev"></Option>
            </Select>
        </div>
    )
};

const SwitchBox = (
    {
        table,
        setTable,
        pagination,
        setPagination
    }
) => {
    const renderBox = () => {
        switch (table) {
            case 'original':
                return (
                    <>
                        <span></span>
                        <div
                            className={styles.switch}
                            onClick={() => setTable('mod')}
                        >
                            
                        </div>
                        <div
                            className={styles.switch}
                            onClick={() => setTable('vip')}
                        >
                            V
                        </div>
                    </>
                );

            case 'mod':
                return (
                    <>
                        <div
                            className={styles.switch}
                            onClick={() => setTable('original')}
                        >
                            
                        </div>
                        <span></span>
                        <div
                            className={styles.switch}
                            onClick={() => setTable('vip')}
                        >
                            V
                        </div>
                    </>
                );

            case 'vip':
                return (
                    <>
                        <div
                            className={styles.switch}
                            onClick={() => setTable('original')}
                        >
                            
                        </div>
                        <div
                            className={styles.switch}
                            onClick={() => setTable('mod')}
                        >
                            
                        </div>
                        <span>V</span>
                    </>
                );

            default:
                throw new Error('');
        }
    };

    return (
        <div className={styles.switchBox}>
            <div className={styles.switchButtons}>
                {renderBox()}
            </div>

            {/* <div
        className={styles.pagination}
        onClick={() => setPagination((prevState) => !prevState)}
      >
        <span>{pagination ? '' : ''}</span>
      </div> */}
        </div>
    )
}

const Title = (
    {
        children
    }
) => (
    <div className={styles.title}>
        <div className={styles.header}>
            <img src={logo} alt="logo" />
            <div className={styles.desc}>
                <h1></h1>
                <p></p>
            </div>
        </div>
        {children}
    </div>
);

const Footer = () => (
    <div className={styles.footer}>
        <div className={styles.left}>
            <div>
                
                <a href="https://space.bilibili.com/49576477">
                    <img src={bilibili} alt="bilibili" />
                    @
                </a>
                &nbsp;
                &nbsp;
                <a href="https://space.bilibili.com/1772592840">
                    <img src={bilibili} alt="bilibili" />
                    @
                </a>
                &nbsp;
                &nbsp;
                <a href="https://www.weibo.com/u/5934299797">
                    <img src={weibo} alt="weibo" />
                    @
                </a>
            </div>

            <div>
                
                <a href="https://kbracer.github.io/">
                    <img src={logo} alt="kbracer" />
                    
                </a>
            </div>
        </div>

        <div className={styles.right}>
            <div>
                
                &nbsp;<a href="https://github.com/facebook/react/">React</a>&nbsp;
                
                &nbsp;<a href="https://github.com/ant-design/ant-design/">Ant-Design</a>&nbsp;
                
            </div>
            <div>
                
                <a href="https://github.com/Phil-Libra">Phil_Libra</a>
                &nbsp;
                JSON
                <a href="https://phil-libra.github.io/excel-to-json/">Excel to JSON</a>
                &nbsp;
                
                <a href="https://github.com/Phil-Libra/kbracer-goldenport">
                    <img src={github} alt="github" />
                    GitHub
                </a>
            </div>

        </div>
    </div>
);

const Description = (
    {
        descStatus,
        setDescStatus
    }
) => (
    <>
        <div className={styles.buttonBox}>
            <button
                className={styles.descButton}
                onClick={() => setDescStatus(true)}
            >
                <span>     </span>
            </button>
        </div>

        <div
            className={styles.description}
            style={{ display: descStatus ? 'block' : 'none' }}
        >
            <CloseCircleTwoTone
                style={
                    {
                        overflow: 'visible',
                        position: 'fixed',
                        top: '-5px',
                        right: '-5px',
                        fontSize: 16
                    }
                }
                twoToneColor="#aaa"
                onClick={() => setDescStatus(false)}
            />
            <div className={styles.manual}>
                <h3>1.</h3>
                <p></p>

                <h3>2.</h3>
                <p>"F"="R"="4"=</p>

                <h3>3.</h3>
                <p>10-25</p>
                <p>200ps30100.X1s</p>
                <p>0</p>
                <p>S55911RS0</p>

                <h3>4.</h3>
                <p></p>

                <h3>5.</h3>
                <p>01</p>
                <p>01100ps112450ps911s992163</p>

                <h3>6.</h3>
                <p>Hot lap</p>
                <p></p>
                <p></p>
            </div>
        </div>
    </>
);

export default App;
